// Color Pallet
$bacground-color: #0a0a0a;
$div-color: #181818;
$hover-color: #2f66ee;
$important-color: #b62323;
$basic-text: #ffffff;
$green: #34a853;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body #root {
  background-color: $bacground-color;
  width: 100%;
  color: white;
  display: flex;
}

main {
  width: 100%;
}

.addContactList {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  // margin-top: 25px;
  h1,
  h2 {
    text-align: center;
    margin-top: 25px;
    margin-bottom: 25px;
  }
}

.underConstruction {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  h1 {
    text-align: center;
    margin-bottom: 26px;
  }
  h2 {
    color: $hover-color;
    margin-bottom: 6px;
  }
}
