@use "../../../App.scss";

.helloWidget {
  display: flex;
  align-items: center;
  background-color: App.$div-color;
  width: 60%;
  margin: 30px auto;
  padding: 10px 5px;
  border-radius: 8px;
  .helloImage {
    width: 120px;
    height: 120px;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
    }
    .img1 {
      width: 116px;
      animation: rotation 22s infinite linear;
    }

    @keyframes rotation {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    .img2 {
      top: 8px;
      left: 8px;
    }
  }

  .helloText {
    h1 {
      font-size: 1.5rem;
    }
    p {
      font-size: 1rem;
      margin-left: 1px;
    }
  }
}
